	module.exports = {"data":{"site":{"siteMetadata":{"title":"Eh, I Could Eat","author":"Jacob Aronoff"}},"markdownRemark":{"id":"/Users/jea/Documents/Code/github/icouldeat/src/pages/chefs-club-counter/index.md absPath of file >>> MarkdownRemark","html":"<h3>Supreme Burger</h3>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/picture-7901a48d660fa83fbb801f227c1c2863-01800.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Supreme Burger\"\n        title=\"\"\n        src=\"/static/picture-7901a48d660fa83fbb801f227c1c2863-f8fb9.jpg\"\n        srcset=\"/static/picture-7901a48d660fa83fbb801f227c1c2863-e8976.jpg 148w,\n/static/picture-7901a48d660fa83fbb801f227c1c2863-63df2.jpg 295w,\n/static/picture-7901a48d660fa83fbb801f227c1c2863-f8fb9.jpg 590w,\n/static/picture-7901a48d660fa83fbb801f227c1c2863-85e3d.jpg 885w,\n/static/picture-7901a48d660fa83fbb801f227c1c2863-d1924.jpg 1180w,\n/static/picture-7901a48d660fa83fbb801f227c1c2863-9452e.jpg 1770w,\n/static/picture-7901a48d660fa83fbb801f227c1c2863-01800.jpg 4032w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>I wanted to come here for the breakfast sandwiches, but unfortunately I was too late, so I settled for a burger. The burger menu was pretty large, as far as burger menus go. I asked the person at the counter for her recommendation and she said to go for the supreme burger. A pretty straight forward burger:  </p>\n<p>Beef, Cheese, Special Sauce, Lettuce, Onions, Pickles, Sesame Bun.</p>\n<p>Overall, it was pretty good, it just wasn’t worth $15. This burger tasted like a classic diner burger, but to pay $15 bucks for a diner burger is 3x more expensive than it should be. If a burger is going to cost that much, it better be the best burger I’ve ever had.</p>\n<p>Rating: 6/10</p>","frontmatter":{"title":"Chefs Club Counter","date":"January 14, 2018"}}},"pathContext":{"slug":"/chefs-club-counter/","previous":{"fields":{"slug":"/ivan-ramen/"},"frontmatter":{"title":"Ivan Ramen"}},"next":{"fields":{"slug":"/brooklyn-bagel-company/"},"frontmatter":{"title":"Brooklyn Bagel & Coffee Company"}}}}

